const swiper = new Swiper('.swiperTwo', {
    // Optional parameters
    direction: 'horizontal',
    loop: false,
    slidesPerView: 3.5,
    initialSlide: 2,
    spaceBetween: 32,
    mousewheel: {
      invert: true,
      eventsTarget: `.swiper-slide`,
      
    },
    speed: 500,
    touchMove: false,
    centeredSlides: true,
    breakpoints: {
      // when window width is >= 320px
      375: {
        slidesPerView: 1,
      },
      // when window width is >= 480px
      576: {
        slidesPerView: 1,
      },
      // when window width is >= 640px
      768: {
        slidesPerView: 2.5,
      },
      1024: {
        slidesPerView: 3.5,
        spaceBetween: 32,
      }
    }
  });
